.ss {
    transform: translate(-50%);
    text-align: center;

    @media (max-width: 767px) {
        font-weight: 600;
        text-align: center;
        display: flex;
        visibility: hidden;
        text-replace: none;
        display: inline-block;
        font-size: 60px;
        transform: translate(50%);
    }
}
